.container-chart {
    height: 250px;
    padding-left: 200px;
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-chart div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
    background-color: #272b2f;
}

svg {
    background: chocolate;
}
/*https://api.thingspeak.com/channels/1062770/feeds.json?api_key=08PEHT6334J9KPBU&results=*/
.container-table {
    width: 100% - 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 4rem;
}
.container-table table {
    width: 80%;
    margin: 2rem;
    font-size: 15pt;
}
.container-table table thead {
    background: #272b2f;
}
th {
    padding: 0.8rem 0.3rem;
    font-size: 15pt;
}
td {
    padding: 0.3rem;
    font-size: 10pt;
    font-weight: 400;
}
