.content-container {
    display: flex;
    align-content: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
}
.big {
    font-size: 100pt;
    margin-bottom: 1rem;
    width: 100%;
}

.content-container a {
    color: #21bf73ce;
}
.content-container a:hover {
    text-decoration: underline;
}
.content-container li {
    padding: 0.2rem;
    list-style-type: none;
}

.content-container p {
    width: 600px;
    font-size: 12.5pt;
}
#list_container {
    width: 900px;
}
