.settings-container {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    width: 100%;
}

.form-container {
    padding: 1rem;
    width: 100%;
}
.form-container label {
    margin-right: 1rem;
    font-size: 18pt;
}
.form-container input {
    color: #ccc;
    background: #212529;
    border-color: #4b545d;
    border-style: ridge;
    padding: 0.5rem;
}

.btn-container {
    display: flex;
    justify-content: center;
    width: 100%;
}

.btn-container input {
    color: #fff;
    background-color: #21bf73;
    border: 1px solid #21bf73;
    box-shadow: 0px 10px 60px rgba(196, 213, 230, 0.2);
    border-radius: 5px;
    font-size: 15pt;
    font-weight: bold;
    padding: 1rem 2rem;
    margin: 2rem 0;
}
